#wpcontent {
  padding: 0;
}

.formello-settings-header {
  padding: 20px 10px;
  background-color: #ffffff;
  box-shadow: 0 1px 0 rgba(213, 213, 213, 0.5), 0 1px 2px #eee;
}
.formello-settings-header .formello-container {
  margin: 0 auto;
  max-width: 750px;
}
.formello-settings-header .formello-container svg {
  height: 40px;
  width: auto;
  margin: 0 1em;
}
.formello-settings-header .formello-container h1 {
  display: flex;
  align-items: center;
  margin: 0;
}

.components-placeholder.formello-settings-placeholder {
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 750px;
  background: #fff;
  border: 1px solid #e2e4e7;
  box-shadow: none;
}

.formello-settings-main {
  margin-left: auto;
  margin-right: auto;
  max-width: 750px;
}
.formello-settings-main .components-card {
  margin: 15px 0;
}
.formello-settings-main .components-card h2 {
  margin: 0;
}
.formello-settings-main .components-card__body > div {
  margin-bottom: 15px;
}
.formello-settings-main .components-card__body label {
  font-weight: bold;
}
.formello-settings-main .components-card__body .components-button {
  margin-right: 1em;
}
.formello-settings-main .formello-action-message {
  padding-left: 10px;
  opacity: 0;
  transition: opacity 200ms ease-in-out;
  color: green;
  pointer-events: none;
}
.formello-settings-main .formello-action-message.formello-action-message--show {
  opacity: 1;
  transition: opacity 200ms ease-in-out;
}
.formello-settings-main .formello-action-message.formello-action-message--error {
  color: red;
}
.formello-settings-main .formello-ratings .dashicons:first-child {
  margin-left: 5px;
}

/* PAGES */
.formello-addons-main {
  padding: 0 1em;
}

.formello-addons {
  display: flex;
  flex-wrap: wrap;
  padding: 1em;
}

.addon {
  display: flex;
  flex-direction: column;
  width: 320px;
  border: 1px solid #ccc;
  margin: 0 20px 20px 0;
  background-color: #fff;
  padding: 10px;
}

.addon img {
  max-width: 100%;
  height: auto;
}

.addon footer {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.addon a {
  box-shadow: none;
}

.addon .type {
  float: right;
  text-transform: uppercase;
  font-weight: bold;
}

.column-id {
  text-align: left;
  width: 5%;
  overflow: hidden;
}

.formello-badge {
  background-color: #337ab7;
  padding: 0 5px;
  margin-left: 10px;
  color: white;
  border-radius: 20%;
}

.column-actions {
  text-align: left;
  width: 150px !important;
  overflow: hidden;
}

.column-formello_date {
  text-align: left;
  width: 150px !important;
  overflow: hidden;
}

.column-formello_icons {
  text-align: left;
  width: 50px !important;
  overflow: hidden;
}

.formello-star {
  color: #e27730;
  font-size: 1rem;
}

.formello-new {
  color: #337ab7;
  font-size: 1rem;
}

/* TABLE SUBMISSION STYLE */
table.formello-bordered {
  font-size: 13px;
  border-collapse: collapse;
  border-spacing: 0;
  background: white;
  width: 100%;
  table-layout: fixed;
}

table.formello-bordered th, table.formello-bordered td {
  border: 1px solid #ddd;
  padding: 12px;
}

table.formello-bordered th {
  width: 160px;
  font-size: 14px;
  text-align: left;
}